import {
  isAllSpace,
  maxLength,
  isChinese,
  isNumber
} from './util.js'
let Validate = {}
Validate.install = function (Vue, options) {

  /**
   * :  type  
   *         required:true 
   * */

  /**/
  const isallspace = (rule, value, callback) => {
    if (value != null && value != "") {
      if (!isAllSpace(value)) {
        return callback(new Error('!'))
      } else {
        return callback()
      }
    } else {
      return callback();
    }
  }

  /**/
  const ischinese = (rule, value, callback) => {
    if (value != null && value != "") {
      if (!isChinese(value)) {
        return callback(new Error(''))
      } else {
        return callback()
      }
    } else {
      return callback();
    }
  }
  /**/
  const isVideoId = (rule, value, callback) => {
    if (value != null && value != "") {
      if (!isChinese(value)) {
        return callback(new Error(''))
      } else if (!isAllSpace(value)) {
        return callback(new Error('!'))
      } else {
        return callback()
      }
    } else {
      return callback();
    }
  }

  /*  */
  const isQuestionArray = (rule, value, callback) => {
    if (value != null && value != "") {
      let arr = value.split(',');
      // debugger;
      let flag = true;
      arr.forEach(element => {
        if (!isNumber(element)) {
          // debugger;
          flag = false;
        } else if (!isAllSpace(element)) {
          flag = false;
        }
      });
      if(flag){
        return callback()
      }else{
        return callback(new Error('ID'))
      }
    } else {
      return callback();
    }
  }

  /**
   *  item
   * required true  
   * maxLength  
   * min  max  min < max  type=number
   * type  mobile
   *         email
   *         url
   *         video_id
   *          src/utils/validate     .......
   * */

  Vue.prototype.filter_rules = function (item) {
    let rules = [];
    if (item.required) {
      rules.push({
        required: true,
        message: '!',
        trigger: 'blur'
      });
    }
    if (item.maxLength) {
      rules.push({
        validator: (rule, value, callback) => {
          if (value != null && value != "") {
            if (!maxLength(value, item.maxLength)) {
              return callback(new Error(`${(item.maxLength / 2)}${(item.maxLength)}`))
            } else {
              return callback()
            }
          } else {
            return callback();
          }
        },
        trigger: ['blur', 'change']
      })
    }
    if (item.max) {
      rules.push({
        min: item.min ? item.min : 0,
        max: item.max,
        message: '' + item.max + '!',
        trigger: ['blur', 'change']
      })
    }
    if (item.type) {
      let type = item.type;
      switch (type) {
        case 'isAllSpace':
          rules.push({
            validator: isallspace,
            trigger: ['blur', 'change']
          });
          break;
        case 'isChinese':
          rules.push({
            validator: ischinese,
            trigger: ['blur', 'change']
          });
          break;
        case 'isVideoId':
          rules.push({
            validator: isVideoId,
            trigger: ['blur', 'change']
          });
          break;
        case 'isQuestionArray':
          rules.push({
            validator: isQuestionArray,
            trigger: ['blur', 'change']
          });
          break;
        default:
          rule.push({});
          break;
      }
    }

    return rules;
  };
};
export default Validate



