import { post, get, put, zDelete } from '../util/zeusAxios.js';
import { getBaseUrl } from '../util/config'
import { formPost, formPut } from "../util/zeusFormAxios";

//
export const getProjectSubject = params => get(`/saas-service/course/manage/get/project/subject/list`, params);

//
export const CourseSyllabuses = params => get(`/saas-service/course-syllabuses`, params);

//
export const ExamSyllabuses = params => get(`/saas-service/examination/syllabuses`, params);

//
export const UpdateCourseSyllabus = (url, param) => put(`/saas-service/course-syllabus/${url}`, param);


//
export const CourseSyllabusTemplates = params => get(`/saas-service/course-syllabus-templates`, params);

//
export const checkSyllabus = (id, params) => get(`/saas-service/course-syllabus/${id}`, params);

//
export const getSyllabusItems = (params) => get(`/saas-service/course/syllabus/items`, params);

//
export const DeleteSyllabusItem = (id, params) => zDelete(`/saas-service/course/syllabus/item/${id}`, params);

//
export const checkResIsInOutline = (syllabus_id, resource_id, params) => get(`/saas-service/course/syllabus/${syllabus_id}/resources/${resource_id}`, params);

//
export const CourseSyllabus = params => formPost(`/saas-service/course-syllabus`, params);

//
export const CourseSyllabusCopy = params => formPost(`/saas-service/course-syllabus/copy`, params);

//
export const selectSyllabus = (id, params) => formPut(`/saas-service/course/syllabus/${id}/template`, params);

//
export const CourseSyllabusItem = params => formPost(`/saas-service/course/syllabus/item`, params);

//
export const ChangeSyllabusItem = (id, params) => formPut(`/saas-service/course/syllabus/item/${id}`, params);

//
export const mountSyllabusResource = (id, params) => formPut(`/saas-service/course/syllabus/item/${id}/resource`, params);


// 
export const getClassListout = parameters => get(`/saas-service/class/lists`, parameters);

// 
export const teachermin = parameters => get(`/saas-service/school/teacher`, parameters);

//
export const checkcoursemit = (projectId, subjectId, params) => get(`/saas-service/plan/class/get/${projectId}/course/${subjectId}`, params);

//
export const checkstudent = (page, pageSize, params) => get(`/saas-service/plan/search/${page}/student/${pageSize}`, params); // /${page}

//
export const checkclass = (studentId, page, pageSize, params) => get(`/saas-service/plan/${studentId}/search/${page}/class/${pageSize}`, params);

// 
export const sortSyllabus = (id, params) => formPut(`/saas-service/course/syllabus/item/${id}/rank`, params);

//
export const getOutlineKnowledgeList = (params) => get(`/caen/v1/backend/knowledge/syllabuses/course`, params);

// 
export const saveOutlineKnowledgeList = (item_id,knowledge_id,params) => formPut(`/toc-service/item/${item_id}/knowledge/${knowledge_id}`, params);

//
export const createSyllabusVideoResource = params => formPost(`/resource-api/resource/video`, params)

//
export const getSubjectTwoList = (params) => get(`/saas-service/subject/two/list`, params);



